
.signDataAddBtn {
    margin-top: 0.5rem;
    background-color: transparent;
    border: none;
    font-family: inherit;
    font-size: 1.1rem;
    color: var(--primary-color);
    font-weight: 500;
    background-color: transparent;
    border: 1px solid var(--primary-color);
    padding: 0.2rem 1rem;
    border-radius: 2rem;
    cursor: pointer;
    height: 3rem;
    display: flex;
    align-items: center;
    transition: all .4s;
    gap: 1rem;
    width: 10rem;
    align-items: center;
    justify-content: center;
}
.signDataAddBtnIcon {
	height: 1rem;
	width: 1rem;
	fill: var(--white);
}
.signDataAddBtn:hover {
	background-color: var(--primary-color);
	border: 1px solid var(--primary-color);
	color: var(--black);
}