/*Desktop View*/

.website_header {
	grid-row: 1 / 2;
	background-color: #fff;
	box-shadow: var(--box-shadow1);
	padding: 2rem 5rem;
	position: fixed;
	width: 100%;
	z-index: 1000;
	display: flex;
	align-items: center;
}


.logo_website {
	height: 8rem;
	position: absolute;
}


.navigation_website {
	margin-left: 10rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}


.navigation__links {
	list-style: none;
	font-size: 1.2rem;
	display: flex;
	gap: 1rem;
	align-items: center;
}

.navigation__link {
	text-decoration: none;
	color: currentColor;
	font-weight: 600;
}

.navigation__active {
	color: #ff0000 !important;
}

.header-icon-in {
	fill: #000000;
	height: 1.5rem;
	width: 1.5rem;
}

.header-right-side {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 1rem;
}

.mobile-option {
	display: none;
}

.mobile-menu {
	display: none;
}

/*Mobile View */

@media (max-width: 768px) {
	.navigation__links--contact {
		display: none !important;
	}

	.mobile-menu {
		display: block;
	}

	.nav-options {
		display: flex;
		width: 100%;
		min-height: 25rem;
		position: absolute;
		top: 10rem;
		left: -100%;
		opacity: 0;
		transition: all 0.5s ease;
		flex-direction: column;
		list-style-type: none;
		grid-gap: 1.5rem;
		padding: 5rem;
	}

	.nav-options.active {
		background: #f9f9f9;
		left: 0rem;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
		align-content: center;
		padding-left: 2rem;
		box-shadow: var(--box-shadow1);
	}

	.menu-icon {
		width: 45px;
		height: 45px;
	}

	.option {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 10vw;
		padding: 30px 0px;
	}

	.navigation__links {
		list-style: none;
		font-size: 1.2rem;
		display: flex;
		grid-gap: 1rem;
		gap: 1rem;
		align-items: start;
		justify-content: center;
	}

	.navigationLoginSidebar {
		left: 25% !important;
	}
}