.homescreen_banner {
    display: flex;
    /* grid-gap: 2rem;
    gap: 2rem; */
    align-items: center;
    justify-content: center;
    height: 60rem;
    padding-top: 10rem;
}

.img-responsive {
    width: 40vh;
    height: auto;
}

.homescreen_banner_button {
    background-color: var(--sky-blue);
    border: 0px;
    color: var(--pure-white);
    height: 5rem;
    width: 25rem;
    border-radius: 1rem;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
}

.homescreen_banner_button:hover {
    background-color: var(--black);
    border: 0px;
    color: var(--pure-white);
    height: 5rem;
    width: 25rem;
    border-radius: 1rem;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
}

.homescreen_banner__subtitle {
    font-weight: 600;
    font-size: 2rem;
}

.homescreen_banner__title {
    font-weight: 300;
}

.homescreen_banner__desc {
    font-weight: 400;
    margin: 2rem 0;
    font-size: 1.5rem;
}

.homescreen_banner_left_padding {
    padding: 0rem 55rem 0rem 18rem;
}

.homescreen_apply-form {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    height: 15rem;
    margin-top: 0rem;
    margin-bottom: 5rem;
}

.homescreen_apply-form-left {
    height: 100%;
    background-color: #00000000;
    width: 60%;
    border-radius: 25px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    transition: 0.3s;
    padding: 3rem 4rem;
    display: flex;
    flex: 1;
    grid-column-gap: 8rem;
    align-items: center;
    justify-content: center;
}

.homescreen_apply-form-right {
    height: 100%;
    background-color: #00000000;
    width: 40%;
    border-radius: 25px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    transition: 0.3s;
    padding: 3rem 4rem;
}

.homescreen_welcome--text {
    text-align: center;
    align-items: center;
    margin-top: 15rem;
    margin-bottom: 15rem;
}

.homescreen_welcome-title {
    color: #244060;
}

.homescreen_licensing--text {
    text-align: center;
    align-items: center;
    margin-top: 10rem;
    margin-bottom: 1rem;
}

.homescreen_licensing-title {
    color: #244060;
}

.homescreen_licensing_products {
    padding: 5rem 10%;
    background-color: var(--sky-blue);
    border-radius: 8rem;
}

.homescreen_licensing_product--list {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 2rem;
}

.licensing__product {
    background-color: #00000000;
    border-radius: 25px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    transition: 0.3s;
    padding: 5rem;
    flex: 1 1;
    grid-gap: 15rem;
    color: var(--pure-white);
    text-decoration: none;
    height: 35rem;
    align-items: center;
    display: flex;
    justify-content: center;
}

.licensing__product:hover {
    box-shadow: 0 8px 16px 0 rgb(0 0 0 / 20%);
}

.licensing__product--heading {
    color: var(--pure-white);
    text-decoration: none;
}

.homescreen_wcu--content {
    display: flex;
    margin-top: 10rem;
    margin-bottom: 10rem;
}

.homescreen_wcu-left {
    border-radius: 25px;
    transition: 0.3s;
    padding: 3rem 4rem;
    width: 100rem;
}

.homescreen_wcu-right {
    height: 35rem;
    background-color: #00bcd4;
    width: 50rem;
    border-radius: 5rem;
    transition: 0.3s;
    padding-top: 2rem;
}

.homescreen_wcu-image {
    margin-left: -5rem;
}

.icon-white {
    fill: white;
    height: 3rem;
    width: 3rem;
}

.flex {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.homescreen_tips--text {
    text-align: center;
    align-items: center;
    margin-top: 10rem;
    margin-bottom: 1rem;
    /* height: 85rem; */
}

.homescreen_tips-title {
    color: #244060;
}

.homescreen_tips-row {
    /* display: flex; */
    align-items: center;
    grid-gap: 2rem;
    gap: 5rem;
    margin: 3rem;
    align-items: center;
    justify-content: center;
}

.homescreen_tips-column {
    background: var(--pure-white);
    height: 30rem;
    width: 45%;
    border-radius: 2rem;
    margin-top: 3rem;
    margin-right: 2rem;
    display: flex;
    float: left;
    /* flex-direction: column; */
}

.homescreen_tips--image {
    width: 30rem;
    height: 28rem;
}

.homescreen_tips-left {
    padding: 1.5rem;
}

.homescreen_tips--btn {
    background: var(--sky-blue);
    border: 0rem;
    border-radius: 2rem;
    height: 10rem;
    width: 5rem;
    color: var(--pure-white);
}

.homescreen_tips-right {
    padding: 2rem 6rem 2rem 0rem;
    text-align: start;
}

.homescreen_tips-arrow-icon {
    fill: var(--sky-blue);
    height: 2rem;
    width: 4rem;
}

.homescreen_tips-read-more {
    text-decoration: none;
    color: var(--sky-blue);
    display: flex;
    align-items: center;
    margin-top: 1rem;
}

.homescreen_apply-form--icon {
    height: 4rem;
    fill: var(--sky-blue);
    width: 3rem;
}

.text-input-prefix--iconBox {
    height: 6.5rem;
    color: var(--sky-blue);
    z-index: 999;
    cursor: pointer;
    transform: translateX(2px);
    transition: 0.3s;
    display: flex;
    align-items: center;
    text-align: center;
}

.homescreen_apply-form--input {
    width: 100%;
    height: 5rem;
    width: 29rem;
    border: 0px solid;
    height: 5rem;
    border-bottom: 2px solid var(--sky-blue);
    background: #f9f9f9;
}

.homescreen_apply-form--inputBox {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    border-radius: 0rem;
    background-color: var(--bg-primary);
    overflow: hidden;
    width: 100%;
    height: 5rem;
}

.text-input-prefix_postfix--iconBox {
    padding: 1.5rem 2rem;
    display: flex;
    align-items: center;
    border-radius: 3.5rem;
    cursor: pointer;
}

.homescreen_apply-form_postfix--icon {
    height: 2rem;
    width: 2rem;
    fill: var(--white);
    cursor: pointer;
}

.product--icon {
    height: 2.5rem;
    width: 2.5rem;
    fill: var(--pure-white)
}

.homescreen_apply-form--input:focus-visible {
    outline: 0;
}

.homescreen_testimonials--text {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.homescreen_chooseus-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

/*Mobile View */

@media (max-width: 768px) {
    .homescreen_tips-column {
        width: 100%;
    }
    .homescreen_apply-form {
        display: flex;
        width: 100%;
        position: relative;
        flex-direction: column;
    }
    .homescreen_apply-form-left {
        width: 100%;
        display: flex;
        flex-direction: column;
        grid-row-gap: 2rem;
        padding: 10%;
    }
    .homescreen_apply-form-right {
        position: relative;
        width: 100%;
        float: left;
    }
    .homescreen_licensing_product--list {
        display: block;
        position: relative;
        width: 100%;
    }
    .homescreen_wcu--content {
        display: flex;
        position: relative;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .homescreen_wcu-left {
        border-radius: 25px;
        transition: 0.3s;
        padding: 3rem 4rem;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .homescreen_banner_left_padding {
        padding: 1.5rem;
        max-width: 35%;

    }
}


.homescreen_products{
    background-color: #000;
    height: 36rem;
    display: flex;
    gap: 2rem;
    padding: 2rem 10rem; 
}

.CardHeader{
    background: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    width:30rem;
}