.container {
	display: grid;
	grid-template-columns: minmax(6rem, 24rem) repeat(1, 1fr);
	height: 100vh;
	width: 100%;
	transition: all 0.5s ease;
}

.mainContent {
	grid-column: 2 / -1;
	overflow: hidden;
	transition: all 0.5s ease;
	margin: 1.5rem 0;
	margin-right: 1.5rem;
	background-color: var(--white);
	/* box-shadow: var(--box-shadow1); */
	border-radius: 2rem;
	position: absolute;
	left: 22rem;
	top: 0;
	right: 0;
	bottom: 0;
	background-size: cover;
	background-repeat: no-repeat;
	background-position-y: 50%;
}

.mainContent__active {
	transition: all 0.5s ease;
	left: 6rem;
}

.content {
	margin-top: 3.5rem;
	height: 85vh;
	overflow-y: auto;
	overflow-x: hidden;
	padding-left: 4rem;
	padding-right: 4rem;
	padding-bottom: 3rem;
	padding-top: 1rem;
}

.not_data_found {
	width: 100%;
	padding: 3rem;
	text-align: center;
	margin-top: 20rem;
	text-transform: capitalize;
}

.flex {
	display: flex;
	gap: .5rem;
	align-items: center;
}

.tableContainer--paginater {
	padding-bottom: 3rem;
	display: flex;
	justify-content: flex-end;
}

.tableContainer {
	width: 100%;
	margin-top: 1rem;
	margin-right: 5rem;
	margin-bottom: 2.5rem;
	background-color: var(--pure-white);
	border-radius: 1rem;
	box-shadow: var(--box-shadow1);
	height: 35vh;
	/* padding-right: 1rem; */
	overflow-y: auto;
	overflow-x: auto;
	/* transition: all .4s; */
}

.table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;
	border-radius: 1rem;
	background-color: var(--pure-white);
	overflow: hidden;
}

th {
	position: sticky;
	top: 0;
	z-index: 100;
}

tr:nth-child(even) {
	background-color: var(--lightest-white);
}

.table tr th {
	white-space: nowrap;
	background-color: var(--light-grey);
	color: var(--dark-color);
	padding: 1rem;
	padding-left: 2rem;
	font-size: 1.3rem;
	font-weight: 600;
	text-align: left;
}

.table tr td {
	white-space: nowrap;
	padding: 1rem;
	padding-left: 2rem;
	color: var(--dark-grey);
	text-align: left;
}

.table tr {
	transition: all 0.8s;
	font-size: 1.2rem;
}

.table__icon {
	height: 1.5rem;
	width: 1.5rem;
}

.table__iconBox {
	display: flex;
	align-items: center;
	/*change this to center*/
	justify-content: left;
	/*change this to center*/
	gap: 0.5rem;
}

.table__button {
	border: none;
	background-color: transparent;
	border: 1px solid var(--primary-color);
	outline: none;
	padding: .7rem;
	border-radius: 50%;
	cursor: pointer;
	transition: all 0.4s;

	display: flex;
	align-items: center;
	position: relative;
}

.table__button span {
	display: none;
	font-size: 1rem;
	color: var(--primary-color);
	background-color: var(--black);
	padding: 0.5rem 1rem;
	border-radius: 10rem;
	position: absolute;
	top: -2rem;
	/* left: 0; */
	/* transform: translate(-50%, -50%); */
	transition: all 1s;
	right: 1rem;
	z-index: 1000;
}

.table__button:hover span {
	display: block;
}

.table__button:hover {
	background-color: var(--primary-color);
	border: 1px solid var(--primary-color);
}

.table__button:hover .table__button--icon {
	fill: var(--white);
}

.table__button--icon {
	height: 1.2rem;
	width: 1.2rem;
	fill: var(--primary-color);
}

.table__button--delete {
	border: 1px solid var(--red);
}

.table__button--delete:hover {
	background-color: var(--red);
	border: 1px solid var(--red);
}

.table__button--delete:hover .table__button--icon-red {
	fill: var(--white);
}

.table__button--icon-red {
	height: 1.2rem;
	width: 1.2rem;
	fill: var(--red);
}

.table__button--second {
	margin-right: 1rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 1rem;

	text-decoration: none;
	color: var(--dark-grey);
	border: 1px solid var(--dark-grey);
	border-radius: 5rem;
	font-size: 1.1rem;
	cursor: pointer;
	transition: all .4s;
}

.table__button--second span {
	margin-left: 1rem;
}

.table__button--secondIconContainer {
	margin: .5rem;
	padding: .5rem;
	background-color: var(--dark-grey);
	border-radius: 50%;
	display: flex;
	align-items: center;
	transition: all .4s;
}

.table__button--secondIcon {
	height: 1rem;
	width: 1rem;
	fill: var(--white);
	transition: all .4s;
}

.table__button--second:hover {
	background-color: var(--dark-grey);
	border: 1px solid var(--dark-grey);
	color: var(--white);
}

.table__button--second:hover .table__button--secondIconContainer {
	background-color: var(--white);
}

.table__button--second:hover .table__button--secondIcon {
	fill: var(--dark-grey);
}

/* Toggle Button Start */
.switch {
	position: relative;
	display: inline-block;
	min-width: 4rem;
	height: 2.5rem;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: var(--light-grey);
	box-shadow: var(--box-shadow-input);
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.slider:before {
	position: absolute;
	content: "";
	height: 1.7rem;
	width: 1.7rem;
	left: 4px;
	bottom: 4.5px;
	background-color: var(--dark-grey);
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.checkbox {
	opacity: 0;
}

.checkbox:checked + .slider {
	background-color: var(--primary-color);
}

.checkbox:focus + .slider {
	box-shadow: 0 0 1px var(--primary-color);
}

.checkbox:checked + .slider:before {
	background-color: var(--white);
	-webkit-transform: translateX(1.3rem);
	-ms-transform: translateX(1.3rem);
	transform: translateX(1.3rem);
}

.slider.round {
	border-radius: 3.4rem;
}

.slider.round:before {
	border-radius: 50%;
}

