.profileDetails {
    padding: 5rem;
    display: flex;
}

.profileCardStats {
    padding: 2rem;
    line-height: 4rem;
}

.profileContact {
    display: flex;
    gap: 2rem;
    font-size: 1.5rem;
}

.profileColumn {
    width: 20rem;
}


.profileContactHeading {
    display: flex;
    gap: 2rem;
    font-size: 1.6rem;
}

.profileLogo {
    height: 8rem;
    padding-left: 2rem;
}

.logindecs {
    font-size: 1.5rem;
}

.profileName {
    font-size: 2.5rem;
}


.trainerContainer {
    margin: 1rem;
    border-radius: 1rem;
    box-shadow: var(--box-shadow1); 
    height: 72rem;
    width: 100%;
	background: var(--bg-primary);
}


.modal__heading {
	border-bottom: 1px solid #ccc;
	padding: 0.5rem 2rem;
	margin-top: 0.5rem;
	margin-bottom: 1rem;
	font-weight: 600;
}
.modal__headingContainer {
	/* display: flex; */
	align-items: center;
    text-align: center;
	justify-content: space-between;
	padding: 0.5rem 2rem;
	margin-top: 2rem;
	margin-bottom: 1rem;
}
.modal__heading2 {
	font-weight: 600;
}


.signDataAddBtn {
	margin-top: .5rem;
	background-color: transparent;
	border: none;
	font-family: inherit;
	font-size: 1.1rem;
	color: var(--primary-color);
	font-weight: 500;
	background-color: transparent;
	border: 1px solid var(--primary-color);
	padding: .2rem 1rem;
	border-radius: 1rem;
	cursor: pointer;
    height: 3rem;
	display: flex;
	align-items: center;
	transition: all .4s;
}
.signDataAddBtnIcon {
	height: 1rem;
	width: 1rem;
	fill: var(--white);
}
.signDataAddBtn:hover {
	background-color: var(--primary-color);
	border: 1px solid var(--primary-color);
	color: var(--black);
}