.event_content {
	grid-column: 1/-1;
	background-color: #ffffff;
	border-radius: 2rem;
	padding: 1rem 5rem;
	overflow: hidden;
	box-shadow: var(--box-shadow1);
	margin: 10rem;
	height: 60vh;
	display: flex;
	align-items: center;
}


.top_card__full {
	grid-column: 1/-1;
	background-color: #ffffff;
	border-radius: 2rem;
	padding: 1rem 5rem;
	overflow: hidden;
	box-shadow: var(--box-shadow1);
	margin: -5rem 15rem;
	height: 15rem;
	display: flex;
	align-items: center;
	flex-direction: column;
    justify-content: center;
}

.top_card__full--heading {
	text-transform: uppercase;
	font-weight: 600;
	font-size: 1.7rem;
}

.top_card__full--items {
	margin-top: 2rem;
	display: flex;
	flex-wrap: wrap;
	gap: 2rem;
	align-items: center;
}

.top_card__full--item {
	width: 25rem;
	display: flex;
	gap: 1rem;
	align-items: center;
}

.top_card__full--itemIcon {
	height: 2rem;
	width: 2rem;
	fill: var(--red);
}

.top_card__full--itemText {
	font-size: 1.4rem;
	font-weight: 500;
}

.registration {
	margin-top: 5rem;
	display: flex;
	gap: 3rem;
	align-items: flex-start;
}

.registration__content {
	background-color: var(--pure-white);
	border-radius: 2rem;
	padding: 2rem 5rem;
}

.registration__info {
	margin-top: 2rem;
}

.registration__content--heading {
	font-size: 1.5rem;
	font-weight: 600;
	color: var(--red);
	margin-bottom: .5rem;
}

.registration__content--text {
	font-size: 1.2rem;
	font-weight: 500;
}

.registration__form {
	margin-top: 2rem;
	padding-bottom: 2rem;
}

.registration__form--inputs {
	margin-top: 2rem;
	display: grid;
	grid-template-rows: repeat(2, 1fr);
	column-gap: 2rem;
	row-gap: 3rem;
}

.registration__form--inputFull {
	grid-column: 1/-1;
}

.registration__form--inputSelectBox {
	display: flex;
	gap: .5rem;
	flex-direction: column;
	width: 20vw;
}

.registration__form--label {
	font-size: 1.2rem;
	font-weight: 500;
}

.registration__form--inputSelect {
	border: none;
	outline: none;
	font-family: inherit;
	background-color: transparent;
	box-shadow: var(--box-shadow1);
	padding: 1rem;
	border-radius: 2rem;
}

.registration__form--nextprev {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.registration__form--title {
	font-size: 1.3rem;
	font-weight: 600;
	margin-bottom: 1rem;
}

.registration__form--price {
	font-size: 1.3rem;
	font-weight: 600;
	margin-bottom: 1rem;
	color: var(--red);
}

.registration__info--heading {
	font-size: 1.5rem;
	font-weight: 600;
	margin-bottom: .5rem;
}

.registration__info--info {
	font-size: 1.2rem;
	font-weight: 500;
	margin-bottom: 1rem;
}

.expiration_date {
	display: flex;
	flex-direction: column;
	gap: .5rem;
}

.expiration_date__label {
	font-size: 1.2rem;
	font-weight: 500;
}

.expiration_date div {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 2rem;
}

.expiration_date div select {
	font-family: inherit;
	font-weight: 500;
	outline: none;
	border: none;
	background-color: var(--bg-primary);
	box-shadow: var(--box-shadow1);
	padding: 1rem;
	border-radius: 2rem;
}

.days_list{
	list-style: none;
    display: flex;
    grid-gap: 9rem;
}