.month {
	width: 100%;
	background: var(--pure-white);
	width: 100%;
	margin-top: 1rem;
	margin-right: 5rem;
	margin-bottom: 2.5rem;
	border-radius: 1rem;
	box-shadow: var(--box-shadow1);
	overflow-y: auto;
	overflow-x: auto;
	background-color: var(--pure-white);
	padding: 4rem;
}

.month ul {
	margin: 0;
	padding: 0;
	list-style: none;
}
.headers {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 1rem;
	margin-bottom: 2rem;
}
.dayMonth {
	text-align: center;
	font-size: 1.3rem;
	line-height: 1.3;
}
.dayMonth span {
	font-size: 1.5rem;
	font-weight: 600;
	display: block;
}
.prevNextBtn {
	background-color: transparent;
	border: none;
	outline: none;
	color: var(--primary-color);
	font-family: inherit;
	font-size: 2rem;
	cursor: pointer;
}
.weekdays {
	margin: 0;
	background-color: var(--light-grey);
	display: flex;
	border-top-left-radius: 2rem;
	border-top-right-radius: 2rem;
}
.weekdays li {
	width: 13.6%;
	padding: 1.2rem 0;
	font-size: 1.5rem;
	color: var(--dark-grey);
	text-align: center;
}
.days {
	padding: 10px 0;
	background: var(--white);
	border-bottom-left-radius: 2rem;
	border-bottom-right-radius: 2rem;
	margin: 0;
	display: flex;
	flex-wrap: wrap;
}
.days li {
	min-width: 13.6%;
	height: 10rem;
	border: 1px solid var(--pure-white);
	text-align: center;
	font-size: 2rem;
	color: var(--dark-grey);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	line-height: 1;
	position: relative;
}
.lockIcon {
	position: absolute;
	top: 1rem;
	right: 1rem;
	height: 1rem;
	width: 1rem;
	fill: var(--red);
}
.sessionData {
	font-size: 1.1rem;
	display: flex;
	align-items: center;
	gap: .5rem;
}
.sessionData span {
	font-size: 3rem;
	color: var(--blue);
}
.days li .active {
	/* padding: .8rem;
	line-height: 1;
	background: var(--primary-color);
	border-radius: 50%; */
	color: var(--primary-color);
	/* border-bottom: 1px solid var(--primary-color); */
}
.totalSection {
	margin: 2rem 0;
	display: flex;
	gap: 1rem;
	align-items: center;
	justify-content: flex-end;
}
.totalSectionData {
	font-size: 1.3rem;
	color: var(--blue);
	display: flex;
	gap: .5rem;
	align-items: center;
}
.totalSectionData span {
	font-size: 1.5rem;
	font-weight: 600;
}
.sessionDataAddBtn {
	margin-top: .5rem;
	background-color: transparent;
	border: none;
	font-family: inherit;
	font-size: 1.1rem;
	color: var(--dark-grey);
	font-weight: 500;
	background-color: transparent;
	border-bottom: 1px solid var(--blue);
	padding: .2rem 1rem;
	border-radius: 1rem;
	cursor: pointer;

	display: flex;
	align-items: center;
	transition: all .4s;
}
.sessionDataAddBtnIcon {
	height: 1rem;
	width: 1rem;
	fill: var(--dark-grey);
}
.sessionDataAddBtn:hover {
	border-bottom: 1px solid var(--dark-grey);
	color: var(--blue);
}
.sessionDataBtn {
	background-color: transparent;
	border: none;
	cursor: pointer;
}
.sessionDataIcon {
	height: 1rem;
	width: 1rem;
	fill: var(--primary-color);
}
.sessionInputs {
	margin-top: .5rem;
	background-color: var(--black);
	border-radius: 1rem;
	padding: .2rem 1rem;
	display: flex;
	gap: 1rem;
	align-items: center;
}
.sessionInput {
	color: var(--white);
	border: none;
	background-color: transparent;
	width: 5rem;
	font-family: inherit;
	outline: none;
}
.sessionInputBtn {
	background-color: transparent;
	border: none;

	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}
.sessionInputIcon_check {
	height: 1.3rem;
	width: 1.3rem;
	fill: var(--primary-color);
}
.sessionInputIcon_close {
	height: 1rem;
	width: 1rem;
	fill: var(--red);
}
.errorText {
	margin-top: .5rem;
	color: var(--red);
	font-size: .8rem;
}

/* Add media queries for smaller screens */
@media screen and (max-width: 720px) {
	.weekdays li,
	.days li {
		width: 13.1%;
	}
}

@media screen and (max-width: 420px) {
	.weekdays li,
	.days li {
		width: 12.5%;
	}
	.days li .active {
		padding: 2px;
	}
}

@media screen and (max-width: 290px) {
	.weekdays li,
	.days li {
		width: 12.2%;
	}
}
