.textBox {
	position: relative;
}

.form_input__error {
	font-size: 1.2rem;
	color: var(--red);
}

.textBox__label {
	position: absolute;
	top: -.5rem;
	left: 0;
	z-index: 10;
	display: flex;
	padding: 1.3rem;
}

.textBox__label--icon {
	height: 2.5rem;
	width: 2.5rem;
	fill: var(--dark-grey);
}

.textBox__input {
	background-color: var(--white);
	border-radius: 0.5rem;
	overflow: hidden;
	width: 25rem;
	display: flex;
	padding-right: 1rem;
}

.textBox__input--box {
	width: 100%;
	margin-left: 5rem;
	background-color: var(--white);
	font-family: inherit;
	border: none;
	outline: none;
	padding: 1rem 1rem;
	color: var(--dark-grey);
}
.textBox__input--box option {
	background-color: var(--white);
	border: none;
	border-color: var(--dark-grey);
	outline: none;
}

.textBox__input--show {
	margin-right: 1rem;
	border: none;
	background-color: transparent;
	cursor: pointer;
}

.textBox__input--show:hover .textBox__input--showIcon {
	fill: #ff3366;
}

.textBox__input--showIcon {
	height: 2rem;
	width: 2rem;
	fill: var(--dark-grey);
	transition: all .4s;
}

.form_input {
	display: flex;
	flex-direction: column;
	gap: .5rem;
	align-items: flex-start;
}

.form_input__label {
	color: var(--light-grey);
	font-size: 1.2rem;
}
