.navigation {
	grid-column: 1 / 2;
	padding-top: 2rem;
	position: fixed;
	width: 22.5rem;
	height: 98vh;
	z-index: 1000;
	transition: all 0.5s;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	transition: all .4s;

	/* background: #fff; */
	border-radius: 12px;
	box-shadow: 0 20px 27px rgb(0 0 0 / 5%);
	margin: 1rem;
	padding: 1rem;
	top: 0rem;
	overflow: hidden;
	background: linear-gradient(to bottom, #ffffffd6 0%, rgb(255 255 255) 10%);
    background-size: 150% 150%;
}

/* .navigation:hover {
	overflow: auto;
} */

.navigation::-webkit-scrollbar {
	width: .5rem;
	height: .5rem;
}

.navigation::-webkit-scrollbar-track-piece {
	background-color: var(--bg-secondary);
}

.navigation::-webkit-scrollbar-thumb {
	background-color: var(--light-grey);
}

.navigation.active {
	width: 7.5rem;
	transition: all 0.5s ease;
}

.navigation__logo {
	height: 10rem;
	padding-left: 4rem;
}

.navigation__logo2 {
	margin-top: 1rem;
	height: 4.5rem;
	display: none;
}

.sidebarnav__button_active{
	left: 1rem !important;
}

.sidebarnav__button {
	position: absolute;
	top: 7%;
	left: 0.2rem;
	transform: translateY(-40%);
	background-color: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	background: rgba(255, 255, 255, 0.829);
	box-shadow: var(--box-shadow-input);
	z-index: 999;
	height: 3rem;
	width: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 26px;
}

.sidebarnav__icon {
	height: 2rem;
	width: 2rem;
	fill: var(--dark-grey);
	left: 18%;
	z-index: 1000;
	transition: all .4s;
	/* height: 2rem;
	width: 2rem;
	fill: var(--dark-grey);
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(10%, 150%);
	z-index: 1000;
	transition: all .4s; */
}

.sidebarnav__icon--box {
	height: 8rem;
	width: 3.07rem;
	fill: #fff;
	position: absolute;
	top: 0;
	left: 0;
	transform: rotate(180deg);
}

.sidebarnav__button:hover .sidebarnav__icon {
	fill: var(--primary-color);
}

.navigation__list {
	margin-top: 3rem;
	width: 100%;
	padding-left: 0px;
	list-style: none;
}

.currentNav {
	/* display: flex;
	align-items: center; */
	position: relative;
}

.activeNav {
	position: absolute;
	top: 1rem;
	right: 0;
	height: 2rem;
	width: 0.5rem;
	background-color: var(--primary-color);
}

.currentNav .navigation__list--itemHover {
	background-color: var(--lightest-blue);
	box-shadow: 0 3px 6px -4px rgb(0 0 0 / 12%), 0 6px 16px 0 rgb(0 0 0 / 8%), 0 9px 28px 8px rgb(0 0 0 / 5%);
    transition: background .3s,width .3s cubic-bezier(.2,0,0,1) 0s;
}

.currentNav .navigation__list--itemHover .navigation__list--link {
	color: var(--black);
	font-weight: 600;
}

.currentNav .navigation__list--itemHover .navigation__list--link .navigation__icon {
	fill: var(--dark-grey);
}

.navigation__list div{
	margin-bottom: 1rem;
}

.navigation__list--item {
	border-radius: 1rem;
	padding: 1rem 0;
	padding-left: 2rem;
	display: none;
	transition: all 0.4s;
	margin-bottom: 1rem;
}

.navigation__list--copyright {
	padding: 1rem 0;
	padding-left: 2rem;
	font-size: 1.2rem;
	font-weight: 500;
	width: 22rem;
}

.navigation__list--copyright span {
	font-size: 1rem;
	font-weight: 600;
	color: var(--dark-grey);
}

.navigation__list--copyright span a {
	font-size: inherit;
	font-weight: inherit;
	color: currentColor;
	text-decoration: none;
	transition: all .4s;
}

.navigation__list--copyright span a:hover {
	color: var(--primary-color);
}

.navigation__list--copyrightHidden {
	padding: 1rem 0;
	padding-left: 2rem;
	font-size: 1.2rem;
	font-weight: 500;
	display: none;
}

.navigation__list--itemHover {
	border-radius: 1rem;
	width: 20rem;
	padding: 1rem 0;
	padding-left: 2rem;
	transition: all 0.4s;
	/* transform: translateX(-20rem); */
	display: block;
	cursor: pointer;
}

.navigation__list--itemHover:hover,
.navigation__list--item:hover {
	background: var(--primary-color);
	transform: translateX(0.2rem);
}

.navigation.active .navigation__list--itemHover {
	display: none;
}

.navigation.active .navigation__list--item {
	display: block;
}

.navigation.active .navigation__list--copyright {
	display: none;
}

.navigation.active .navigation__list--copyrightHidden {
	display: block;
}

.navigation.active .navigation__logo {
	display: none;
}

.navigation.active .navigation__logo2 {
	display: block;
}

.navigation__list--link {
	text-decoration: none;
	color: var(--dark-grey);
	font-size: 1.2rem;
	font-weight: 500;

	display: flex;
	gap: 1rem;
}

.navigation__list--link--span {
	display: flex;
	align-items: center;
	gap: 1.2rem;
}

.navigation__list--link2 {
	text-decoration: none;
	color: var(--dark-grey);
	font-size: 1.2rem;
	font-weight: 500;

	display: flex;
	gap: 1rem;
}

.navigation__icon {
	height: 1.5rem;
	width: 1.5rem;
	fill: var(--dark-grey);
}

.navigation__icon2 {
	height: 1.5rem;
	width: 1.5rem;
	fill: var(--dark-grey);
}

.hasChildren {
	position: relative;
}

.hasChildren::after {
	cursor: pointer;
	padding-right: 1rem;
	content: ' \002B';
	color: var(--dark-grey);
	font-size: 1.7rem;
	position: absolute;
	right: 0rem;
	top: .7rem;
}

/* .subMenuActive {
	background-color: var(--primary-color);
} */
.subMenuActive.hasChildren::after {
	content: ' \002D';
}

.subMenuActive.hasChildren ul {
	display: block;
}

.navigation__sub__active {
	color: var(--black) !important;
	font-weight: 600 !important;
}

.navigation__sub__active svg {
	fill: var(--black) !important;
}

.hasChildren ul {
	margin-top: 1rem;
	display: none;
	list-style: none;
}

.hasChildren ul li {
	padding: .5rem 1rem;
	transition: all .4s;
}

.hasChildren ul li:hover {
	background-color: var(--yellow);
}

.hasChildren ul li a {
	text-decoration: none;
	color: var(--dark-grey);
	font-size: 1.1rem;
	font-weight: 500;
	display: flex;
	gap: .5rem;
	align-items: center;
}

.hasChildren ul li a svg {
	height: 1.2rem;
	width: 1.2rem;
	fill: var(--dark-grey);
}

.navigation__list--link2--logout {
	background: transparent;
	box-shadow: none;
	border: 0;
	font-family: 'Poppins', sans-serif;
	cursor: pointer;
}