.button,
.button__delete {
	border-radius: .5rem;
	font-family: inherit;
	font-size: 1.3rem;
	padding: 1rem 3rem;
	color: var(--white);
	cursor: pointer;

	display: flex;
	align-items: center;
	gap: .5rem;
	transition: all .4s;
}
.button {
	background-color: var(--dark-grey);
	border: 1px solid var(--dark-grey);
}

.button .button__icon {
	height: 1.5rem;
	width: 1.5rem;
	fill: var(--white);
	transition: all .4s;
}
.button:hover {
	background-color: transparent !important;
	border: 1px solid var(--dark-grey);
	color: var(--dark-grey);
}
.button:hover .button__icon {
	fill: var(--dark-grey);
}

.button__delete {
	background-color: var(--red);
	border: 1px solid var(--red);
}
.button__delete .button__icon {
	height: 1.5rem;
	width: 1.5rem;
	fill: var(--white);
	transition: all .4s;
}
.button__delete:hover {
	background-color: transparent;
	border: 1px solid var(--red);
	color: var(--red);
}
.button__delete:hover .button__icon {
	fill: var(--red);
}
