.login_container_side_image {
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 80%;
	height: auto;
}


.login_container__logo {
	height: 15rem;
}


.login_container__form {
	padding: 3rem 5rem;
	display: flex;
	grid-gap: 1rem;
	gap: 1rem;
	flex-direction: column;
	color: #6e6b7b;
	transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
	align-items: center;
	justify-content: center;
}


.login_container__heading {
	color: #6e6b7b;
	font-weight: 200;
	font-size: 3rem;
	text-align: center;
}

.login_container__subheading {
	color: #6e6b7b;
	font-weight: 500;
	font-size: 1.3rem;
	text-align: center;
	margin-bottom: 1rem;
}

.login_container__main {
	display: flex;
	grid-template-columns: minmax(6rem, 24rem) repeat(1, 1fr);
	height: 100vh;
	width: 100%;
	transition: all 0.5s ease;
}